<template>
  <div class="header-news">
    <ul class="header-news-container">
      <li v-for="news in latestNews" :key="news.id">
        <LatestNewsItemWrapper :news-url="news.newsUrl">
          <LatestNewsItem
            :content="news.newsContent"
            :is-date-shown="news.isDateShown"
            :published-at="news.publishedAt"
          />
        </LatestNewsItemWrapper>
      </li>
    </ul>
  </div>
</template>

<script>
import LatestNewsItemWrapper from '@/components/LatestNewsItemWrapper.vue';
import LatestNewsItem from '@/components/LatestNewsItem.vue';

export default {
  name: 'LatestNews',
  components: {
    LatestNewsItemWrapper,
    LatestNewsItem,
  },
  computed: {
    latestNews() {
      const { currentDateTime } = this.$store.state;
      const allNews = this.$store.state.latestNews;
      return allNews
        .filter(news => news.publishedAt <= currentDateTime)
        .slice(0, 4);
    },
  },
};
</script>

