<template>
  <div class="home">
    <header class="container">
      <TopNavigation />
      <Header />
    </header>
    <div class="live-video-container">
      <div class="live-video-row">
        <LiveVideo
          title="A"
          slot-id="slot1"
          :window-inner-width="windowInnerWidth"
          :window-inner-height="windowInnerHeight"
        />
        <LiveVideo
          title="B"
          slot-id="slot2"
          :window-inner-width="windowInnerWidth"
          :window-inner-height="windowInnerHeight"
        />
      </div>
      <div class="live-video-row">
        <LiveVideo
          title="C"
          slot-id="slot3"
          :window-inner-width="windowInnerWidth"
          :window-inner-height="windowInnerHeight"
        />
        <LiveVideo
          title="D"
          slot-id="slot4"
          :window-inner-width="windowInnerWidth"
          :window-inner-height="windowInnerHeight"
        />
      </div>
    </div>
  </div>
</template>

<script>
import _ from 'lodash';
import LiveVideo from '@/components/LiveVideo.vue';
import TopNavigation from '@/components/TopNavigation.vue';
import Header from '@/components/Header.vue';

export default {
  name: 'home',
  components: {
    LiveVideo,
    TopNavigation,
    Header,
  },
  data() {
    return {
      windowInnerHeight: NaN,
      windowInnerWidth: NaN,
      debouncedResizeHandler: undefined,
    };
  },
  created() {
    this.debouncedResizeHandler = _.debounce(() => {
      this.$nextTick(() => this.updateWindowSize());
    }, 50);
    window.addEventListener('resize', this.debouncedResizeHandler);
  },
  mounted() {
    this.updateWindowSize();
    this.$ga.page('/');
  },
  destroyed() {
    window.removeEventListener('resize', this.debouncedResizeHandler);
    this.debouncedResizeHandler = undefined;
  },
  methods: {
    updateWindowSize() {
      this.windowInnerWidth = window.innerWidth;
      this.windowInnerHeight = window.innerHeight;
    },
  },
};
</script>

<style lang="scss">
.home {
  .container {
    margin: 0 auto;
  }
  header {
    padding: 0 10px;
    max-width: 1000px;
  }
  .live-video-container {
    font-size: 0;
    .live-video-row {
      text-align: center;
      font-size: 0;
