<template>
  <div class="news-item">
    <span class="time">{{ formattedPublishedAt }}</span>
    <p class="news-title">{{ content }}</p>
  </div>
</template>

<script>
import moment from 'moment';

export default {
  name: 'LatestNewsItem',
  props: {
    publishedAt: Date,
    isDateShown: Boolean,
    content: String,
  },
  computed: {
    formattedPublishedAt() {
      return this.isDateShown
        ? moment(this.publishedAt).format('hh:mm DD/MM')
        : moment(this.publishedAt).format('HH:mm');
    },
  },
};
</script>

<style lang="scss">
.news-item {
  display: flex;
  align-items: center;
  max-width: 100%;
  .time {
    display: inline-block;
    margin-right: 15px;
  }
  .news-title {
    flex: 1 1 auto;
    margin: 0;
  }
}
</style>
