<template>
  <div id="app">
    <router-view />
    <div id="fb-root"></div>
  </div>
</template>

<script>
import actionTypes from '@/store/action-types';

const initFacebookScript = (d, s, id) => {
  const fjs = d.getElementsByTagName(s)[0];
  if (d.getElementById(id)) return;
  const js = d.createElement(s);
  js.id = id;
  js.src = 'https://connect.facebook.net/en_US/sdk.js#xfbml=1&version=v3.0';
  fjs.parentNode.insertBefore(js, fjs);
};

export default {
  name: 'App',
  data() {
    return {
      timeUpdateIntervalHandle: null,
      siteDataUpdateIntervalHandle: null,
    };
  },
  beforeCreate() {
    this.$store.dispatch(actionTypes.FETCH_SITE_DATA);
    this.siteDataUpdateIntervalHandle = window.setInterval(() => {
      this.$store.dispatch(actionTypes.FETCH_SITE_DATA);
    }, 60000);
    this.$store.dispatch(actionTypes.UPDATE_CURRENT_DATE_TIME);
    this.timeUpdateIntervalHandle = window.setInterval(() => {
      this.$store.dispatch(actionTypes.UPDATE_CURRENT_DATE_TIME);
    }, 1000);

    initFacebookScript(document, 'script', 'facebook-jssdk');
  },
};
</script>

