<template>
  <div class="live-video">
    <div class="live-video-header">
      <div class="title" :class="`title-${title}`">{{ title }}</div>
      <div class="channel-select-wrapper">
        <select class="channel-select" v-model="selectedChannelId" ref="channelSelect">
          <option
            v-for="option in liveChannelOptions"
            :key="option.value"
            :value="option.value"
          >{{ option.name }}</option>
        </select>
      </div>
    </div>
    <div class="video" ref="video" :style="{ height: videoContainerHeightStyle }">
      <iframe
        :src="iframeSourceUrl"
        :style="{ height: `${videoHeight}px` }"
        :class="{ loaded: iframeSourceUrl }"
        allowfullscreen="allowfullscreen"
        frameborder="0"
        scrolling="no"
      ></iframe>
    </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex';

export default {
  name: 'LiveVideo',
  props: {
    title: String,
    windowInnerWidth: Number,
    windowInnerHeight: Number,
    slotId: String,
  },
  data() {
    return {
      videoWidth: 0,
      videoHeight: 0,
    };
  },
  computed: {
    ...mapGetters({
      liveChannelOptions: 'liveChannelSelectOptions',
    }),
    videoSource() {
      if (this.selectedChannelId === '-1') {
        return null;
      }
      return this.$store.getters.getVideoSourceByChannelId(
        this.selectedChannelId,
      );
    },
    iframeSourceUrl() {
      if (!this.videoSource || !this.videoWidth || !this.videoWidth) {
        return undefined;
      }
      return `https://www.facebook.com/plugins/video.php?href=${this.videoSource}&height=${this.videoHeight}&width=${this.videoWidth}`;
    },
    videoContainerHeightStyle() {
      return this.windowInnerWidth < 768 ? `${this.videoHeight}px` : '';
    },
    selectedChannelId: {
      get() {
        return this.$store.getters.getSelectedChannelIdBySlotId(this.slotId);
      },
      set(value) {
        const config = {
          channelId: value,
          isManuallySelected: true,
        };
        return this.$store.dispatch('UPDATE_CHANNEL_SLOT_CONFIG', {
          config,
          slotId: this.slotId,
        });
      },
    },
  },
  watch: {
    windowInnerWidth() {
      this.videoWidth = this.$refs.video.clientWidth;
    },
    windowInnerHeight() {
      if (this.windowInnerWidth < 768) {
        return;
      }
      this.$nextTick(() => {
        this.videoHeight = this.$refs.video.clientHeight;
      });
    },
    videoWidth() {
      if (!this.videoWidth) {
        this.videoHeight = 0;
        return;
      }
      if (this.windowInnerWidth < 768) {
        this.videoHeight = Math.ceil((this.videoWidth * 9) / 16);
        return;
      }
      this.$nextTick(() => {
        this.videoHeight = this.$refs.video.clientHeight;
      });
    },
  },
};
</script>

