import Vue from 'vue';
import Vuex from 'vuex';
import VueAnalytics from 'vue-analytics';

import 'babel-polyfill';

import { fetch as whatwgFetch } from 'whatwg-fetch';

import actionTypes from './action-types';
import mutationTypes from './mutation-types';

Vue.use(VueAnalytics, {
  id: 'UA-57992226-14',
  set: [
    {
      field: 'appversion',
      value: process.env.VUE_APP_VERSION,
    },
  ],
});
Vue.use(Vuex);

const SITE_DATA_ENDPOINT = '/api/site-data.json';
export default new Vuex.Store({
  state: {
    currentDateTime: new Date(),
    latestNews: [],
    liveChannels: {},
    lastUpdatedAt: null,
    channelSlotConfigs: {
      slot1: { channelId: 'offline' },
      slot2: { channelId: 'offline' },
      slot3: { channelId: 'offline' },
      slot4: { channelId: 'offline' },
    },
  },
  getters: {
    liveChannelIds: state => Object.keys(state.liveChannels),
    getLiveChannelById: state => (id => state.liveChannels[id]),
    liveChannelSelectOptions: (state, getters) => {
      const liveChannelIds = Object.keys(state.liveChannels);
      const options = liveChannelIds.map((id) => {
        const channel = getters.getLiveChannelById(id);
        return {
          name: channel.channelName,
          value: String(channel.id),
          order: channel.order,
        };
      }).sort((optionA, optionB) => optionA.order - optionB.order);
      options.push({
        name: 'Offline',
        value: 'offline',
      });
      return options;
    },
    getVideoSourceByChannelId: (state, getters) => (id) => {
      const channel = getters.getLiveChannelById(id);
      if (!channel) {
        return undefined;
      }
      return channel.facebookVideoUrl;
    },
    channelSlotIds: state => Object.keys(state.channelSlotConfigs),
    selectedChannelIds: state => Object.values(state.channelSlotConfigs)
      .filter(config => config.channelId !== 'offline')
      .map(config => String(config.channelId)),
    getChannelSlotConfigById: state => (id => state.channelSlotConfigs[String(id)]),
    getSelectedChannelIdBySlotId: state => ((slotId) => {
      const config = state.channelSlotConfigs[slotId];
      return String(config.channelId);
    }),
  },
  mutations: {
    [mutationTypes.SAVE_CURRENT_DATE_TIME](state, { newCurrentDateTime }) {
      state.currentDateTime = newCurrentDateTime;
    },
    [mutationTypes.SAVE_LAST_UPDATED_AT](state, { newLastUpdatedAt }) {
      if (state.lastUpdatedAt && state.lastUpdatedAt >= newLastUpdatedAt) {
        return;
      }
      state.lastUpdatedAt = newLastUpdatedAt;
    },
    [mutationTypes.SAVE_LATEST_NEWS](state, { newLatestNews }) {
      state.latestNews = newLatestNews;
    },
    [mutationTypes.SAVE_LIVE_CHANNEL](state, { liveChannel }) {
      Vue.set(state.liveChannels, liveChannel.id, liveChannel);
    },
    [mutationTypes.REMOVE_LIVE_CHANNEL](state, { liveChannelId }) {
      if (!Object.prototype.hasOwnProperty.call(state.liveChannels, liveChannelId)) {
        return;
      }
      Vue.delete(state.liveChannels, liveChannelId);
    },
    [mutationTypes.SAVE_CHANNEL_SLOT_CONFIG](state, { slotId, config }) {
      Vue.set(state.channelSlotConfigs, slotId, config);
    },
  },
  actions: {
    [actionTypes.UPDATE_CURRENT_DATE_TIME]({ commit }) {
      commit(mutationTypes.SAVE_CURRENT_DATE_TIME, {
        newCurrentDateTime: new Date(),
      });
    },
    [actionTypes.UPDATE_LIVE_CHANNELS]({ commit, getters }, { newLiveChannels }) {
      const newLiveChannelIds = newLiveChannels.map(channel => String(channel.id));
      const existingLiveChannelIds = getters.liveChannelIds;
      const toBeReomvedLiveChannelIds = existingLiveChannelIds.filter(
        id => !newLiveChannelIds.includes(id),
      );
      toBeReomvedLiveChannelIds.forEach((liveChannelId) => {
        commit(mutationTypes.REMOVE_LIVE_CHANNEL, { liveChannelId });
      });

      newLiveChannels.forEach((channel) => {
        const channelId = channel.id;
        const existingChannel = getters.getLiveChannelById(channelId);
        const transformedChannel = {
          ...channel,
          updatedAt: new Date(channel.updatedAt),
        };
        if (existingChannel && existingChannel.updatedAt === transformedChannel.updatedAt) {
          return;
        }
        commit(mutationTypes.SAVE_LIVE_CHANNEL, {
          liveChannel: transformedChannel,
        });
      });

      const { selectedChannelIds } = getters;
      const slotIds = getters.channelSlotIds;
      let assignedChannelCount = 0;
      const availableChannelIds = newLiveChannelIds.filter(id => !selectedChannelIds.includes(id));
      slotIds.forEach((id) => {
        const slotConfig = getters.getChannelSlotConfigById(id);
        const selectedChannelId = slotConfig.channelId;
        if (selectedChannelId !== 'offline' && newLiveChannelIds.includes(selectedChannelId)) {
          return;
        }

        const newChannelId = assignedChannelCount < availableChannelIds.length
          ? availableChannelIds[assignedChannelCount] : 'offline';
        commit(mutationTypes.SAVE_CHANNEL_SLOT_CONFIG, {
          slotId: id,
          config: {
            channelId: newChannelId,
          },
        });
        assignedChannelCount += 1;
      });
    },
    [actionTypes.UPDATE_LATEST_NEWS]({ commit }, { newLatestNews }) {
      const transformedLatestNews = newLatestNews.map(rawNews => ({
        id: rawNews.id,
        newsContent: rawNews.newsContent,
        newsUrl: rawNews.newsUrl,
        isDateShown: !!rawNews.isDateShown,
        publishedAt: new Date(rawNews.publishedAt),
        updatedAt: new Date(rawNews.updatedAt),
      }));
      commit(mutationTypes.SAVE_LATEST_NEWS, {
        newLatestNews: transformedLatestNews,
      });
    },
    [actionTypes.FETCH_SITE_DATA]({ state, commit, dispatch }) {
      return whatwgFetch(SITE_DATA_ENDPOINT)
        .then(res => res.json())
        .then((siteData) => {
          if (!siteData || !siteData.updatedAt
            || !(siteData.latestNews instanceof Array)
            || !(siteData.facebookLiveChannels instanceof Array)) {
            return;
          }

          const newLastUpdatedAt = new Date(siteData.updatedAt);
          if (state.lastUpdatedAt && newLastUpdatedAt <= state.lastUpdatedAt) {
            return;
          }
          commit(mutationTypes.SAVE_LAST_UPDATED_AT, { newLastUpdatedAt });

          const newLatestNews = siteData.latestNews || [];
          dispatch(actionTypes.UPDATE_LATEST_NEWS, { newLatestNews });

          const newLiveChannels = siteData.facebookLiveChannels || [];
          dispatch(actionTypes.UPDATE_LIVE_CHANNELS, { newLiveChannels });
        })
        .catch(err => console.error(err));
    },
    [actionTypes.UPDATE_CHANNEL_SLOT_CONFIG]({ commit }, { slotId, config }) {
      commit(mutationTypes.SAVE_CHANNEL_SLOT_CONFIG, { slotId, config });
    },
  },
});
